import React, { useState } from 'react';
import { connect } from 'react-redux';
import NoSSR from '@mpth/react-no-ssr';
import { StyledDetails, StyledDetailsImg, StyledDetailsList, StyledDetailsListItem, StyledName } from '../peopleInfo/peopleInfo.styled';
import ErrorMessage from "../../errorMessage/errorMessage";
import Spinner from '../../spinner/spinner';
import { setBroken } from '../../../actions'

const PeopleInfo = ({ selectedPerson, isFetchingPeople, broken, setBroken }) => {
  const serverImage =
    <StyledDetailsImg
      planet
      onError={() => setBroken()}
      src={`https://starwars-visualguide.com/assets/img/characters/${selectedPerson.id}.jpg`} />
  const brokenImg = broken ? <ErrorMessage /> : serverImage;

  return (
    <>
      <StyledDetails>
        {isFetchingPeople ? <Spinner /> :
          <>
            <StyledName>{selectedPerson.name}</StyledName>
            {brokenImg}
            <StyledDetailsList>
              <StyledDetailsListItem>Height:{selectedPerson.height}</StyledDetailsListItem>
              <StyledDetailsListItem>Mass: {selectedPerson.mass}</StyledDetailsListItem>
              <StyledDetailsListItem>Hair color: {selectedPerson.hair_color}</StyledDetailsListItem>
              <StyledDetailsListItem>Skin color: {selectedPerson.skin_color}</StyledDetailsListItem>
              <StyledDetailsListItem>Eye color: {selectedPerson.eye_color}</StyledDetailsListItem>
              <StyledDetailsListItem>Birth year: {selectedPerson.birth_year}</StyledDetailsListItem>
              <StyledDetailsListItem>Gender: {selectedPerson.gender}</StyledDetailsListItem>
            </StyledDetailsList>

          </>
        }
      </StyledDetails>
    </>
  )
}
const mapStateToProps = (state) => {
  return {
    selectedPerson: state.selectedPerson,
    isFetchingPeople: state.isFetchingPeople,
    broken: state.broken

  }
}



export default connect(mapStateToProps, { setBroken })(PeopleInfo);